/th>\n        <th>维修厂电话</th>\n        <th>负责人</th>\n        <th>地址</th>\n        <th>已发货数量</th>\n      </tr>\n      <tr v-for=\"item in list\">\n        <td>{{item.company}}</td>\n        <td>{{item.phone}}</td>\n        <td>{{item.leader}}</td>\n        <td>{{item.province + item.city + item.address}}</td>\n        <td>\n          <el-button type=\"text\"\n                     @click=\"examineDetails(item.num)\">{{item.number}}</el-button>\n        </td>\n      </tr>\n    </table>\n    <!-- 模态框 -->\n    <el-dialog title=\"详情\"\n               :visible.sync=\"dialogTableVisible\"\n               :center=true>\n      <el-table :data=\"listChil\">\n        <el-table-column property=\"man\"\n                         label=\"姓名\"\n                         align=\"center\"></el-table-column>\n        <el-table-column property=\"phone\"\n                         label=\"电话\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"地址\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n                       <span>{{scope.row.address}}{{scope.row.details}}</span>\n                      </template>\n        </el-table-column>\n        <el-table-column property=\"hair_time\"\n                         label=\"收货时间\"\n                         align=\"center\"></el-table-column>\n      </el-table>\n    </el-dialog>\n\n    <!-- 分页 -->\n    <div :class=\"list.length<=7?'page-nav':'page_center'\"\n         v-show=\"list.length>0\">\n      <paging :page-count=\"pageCount\"\n              :page=\"page\"\n              @index=\"paging\"></paging>\n      <!--分页的组件-->\n    </div>\n  </div>\n</template>\n<script>\nexport default {\n  data () {\n    return {\n      token: window.sessionStorage.getItem('bbytoken'),\n      list: [],\n      page: 1, //当前页\n      pageCount: 1,//总页数\n      detailsVisible: false, //详情弹框状态\n      ReasonVisible: false, //驳回理由状态\n      information: {}, //保单信息\n      dialogTableVisible: false,//默认模态框状态,\n      listChil: [] //模态框数据\n    }\n  },\n  methods: {\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/PolicyAudit/yearsOrders', { token: this.token, page: this.page }).then(res => {\n        if (res.data.code == 1) {\n          this.list = res.data.data.list\n          this.pageCount = res.data.data.rows;\n        \n        } else {\n          layer.msg(res.data.msg, { time: 1500 })\n        }\n      }).catch(err => console.log(err))\n    },\n    examineDetails (value) {\n      this.dialogTableVisible = true\n      this.listChil = value\n   \n    }\n  },\n  mounted () {\n    this.init()\n  }\n}\n</script>\n<style scoped>\n.table a {\n  color: #04bbfc;\n}\n.details {\n  font-size: 12px;\n}\n.information > dt {\n}\n.details table td {\n  font-size: 12px;\n  border: none;\n}\n.Insurancetype {\n  margin-top: 20px;\n}\n.Insurancetype table {\n  border: 1px solid #dbdbdb;\n}\n.Insurancetype table th {\n  background: none;\n  width: 50%;\n  border: 1px solid #dbdbdb;\n  font-size: 16px;\n}\n.Insurancetype table td {\n  border: 1px solid #dbdbdb;\n}\n.Photo {\n  text-align: center;\n  margin-top: 20px;\n  overflow-x: auto;\n}\n\n.Photo > ul {\n  margin: 0 auto;\n  display: flex;\n  justify-content: center;\n}\n.Photo > ul > li {\n}\n.Photo img {\n  text-align: center;\n  max-height: 100px;\n  max-height: 100px;\n  padding-left: 10px;\n}\n.content > textarea {\n  border: none;\n  width: 100% !important;\n  resize: none;\n  height: 100px;\n  max-height: 300px !important;\n}\n</style>\n<style>\n.adopt .el-dialog__header {\n  background: none;\n  padding-bottom: 20px;\n  border-bottom: 1px solid #e6e6eb;\n}\n.adopt .el-dialog__footer {\n  border-top: 1px solid #e6e6eb;\n  padding-bottom: 10px;\n}\n.Reason .el-dialog__header {\n  padding-bottom: 20px;\n  border-bottom: 1px solid #e6e6eb;\n}\n.Reason .el-dialog__footer {\n  border-top: 1px solid #e6e6eb;\n  padding-bottom: 10px;\n}\n</style>\n"],"so